function Report() {
  var marks = 0;

  for (let ans in answer) {
    if (localStorage.getItem(ans.no) === ans.ans) {
      marks = marks + 1;
    }
  }

  return (
    <div>
      <h2>
        {localStorage.getItem("name")}
        <hr />
        <ol>
          {answer.map((ans) => (
            <li>
              <img
                className="qtn-img"
                src={`Images/${ans.no}.PNG`}
                alt="oops"
              />
              <br />
              Your response:
              {localStorage.getItem(ans.no)
                ? localStorage.getItem(ans.no)
                : "__"}{" "}
              <br />
              Answer: {ans.ans}
              <br />
              <br />
              <hr />
            </li>
          ))}
        </ol>
        Marks Obtained: {marks}
      </h2>
    </div>
  );
}

export default Report;
