import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./styles.css";

const Home = () => {
  const [name, setName] = useState("");
  localStorage.clear();
  return (
    <>
      <div className="Center_karo">
        <div className="Name_area">
          <h1>JEE Mains 2021</h1>
          <h3>August 26 (First Shift)</h3>
          <br />
          <label htmlFor="nm">Enter Name: </label>
          <input
            id="nm"
            autoComplete="off"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <br />
          <br />
          <Link
            className="nav-btn"
            to={"/1"}
            onClick={() => {
              localStorage.setItem("name", name);
            }}
          >
            Start
          </Link>
        </div>
      </div>
    </>
  );
};

export default Home;
