import React from "react";
import arr from "./objects/Question_array.js";
import "./styles.css";

export default function Navigation(id) {
  const nb = (val) => {
    return (
      <a
        className={
          id.id == val
            ? "curr-qtn"
            : localStorage.getItem(val)
            ? "solved-qtn"
            : "nav-btn"
        }
        href={`/${val}`}
      >
        {val}
      </a>
    );
  };
  return (
    <div className="Question_pallete">
      <form>{arr.map(nb)}</form>
    </div>
  );
}
