import React, { useState } from "react";
import Redirect from "react-router-dom";
import { Link, useParams, useNavigate } from "react-router-dom";
import Navigation from "./Navigation.jsx";
import Samay from "./samay.jsx";
import "./styles.css";

const noOfQuestions = 90;

const Question = (props) => {
  const Navigate = useNavigate();
  const params = useParams();

  const [formData, setFormData] = useState({
    is_agree: false,
    ans: ""
  });

  const handleChange = (event) => {
    const target = event.target;
    const name = target.name;
    const value = target.value;

    setFormData({
      ...formData,
      [name]: value
    });

    localStorage.setItem(params.id, value);
  };

  return (
    <>
      <Samay />
      <div className="qtn-part">
        <form
          onSubmit={(e) => {
            e.preventDefault();
            Navigate(
              params.id < noOfQuestions ? `/${Number(params.id) + 1}` : "/1"
            );
          }}
        >
          <h1>Question {params.id}</h1>
          <img className="qtn-img" src={`Images/${params.id}.PNG`} alt="oops" />
          <br />
          <br />
          <sup>** Enter answer as 1, 2, 3, 4 for options a, b, c, d **</sup>
          <br />
          <br />
          {/* <input type="radio" name="ans" value="a" onChange={handleChange} />
          <label> a </label>
          <br />
          <input type="radio" name="ans" value="b" onChange={handleChange} />
          <label> b </label>
          <br />
          <input type="radio" name="ans" value="c" onChange={handleChange} />
          <label> c </label>
          <br />
          <input type="radio" name="ans" value="d" onChange={handleChange} />
          <label> d </label>
          <br />
          <br />
          Option Selected: {localStorage.getItem(params.id)} */}
          <label> Answer: </label>
          <input
            name="ans"
            autoComplete="off"
            defaultValue={localStorage.getItem(params.id)}
            onChange={handleChange}
          />
          <br />
          <br />
          Your Answer: {localStorage.getItem(params.id)}
          <div className="btn-area">
            <button className="btn" type="submit">
              Next
            </button>

            {/* <input
              className="btn"
              type="reset"
              value="Reset"
              onClick={() => {
                localStorage.removeItem(`${params.id}`);
              }}
            /> */}

            <a className="nav-btn" href="report">
              Submit
            </a>
          </div>
        </form>
        <Navigation id={params.id} />
      </div>
    </>
  );
};

export default Question;
